.App {
  background-color: #f7f7f7;
}

.App-header {
  background-color: #333;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  border-top: 2px solid cyan;
  overflow: hidden;
}

.navbar {
  margin-bottom: 0!important;
  padding: 5px;
}

.navbar .menuIconDiv {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
}

.navbar .menuIcon {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  transform: rotate(-180deg);
  transition: all .5s ease;
}

.navbar .menuIcon.visible {
  opacity: 1;
  transform: rotate(0deg);
  transition: all .5s ease;
}

.navItem {
  max-height: 0;
  transition: max-height .5s ease-out;
}

.navItem .menu .menu {
  padding: 5px;
}

.navItem.mobile_open {
  max-height: 100vh;
  transition: max-height .5s ease-out;
}

.App-logo {

}

.App-body {
  padding: 2vh;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1em;
  color: black;
}
